import Vue from 'vue';
import Vuex from 'vuex';
import api from './api/index.js';
import date from "./components/date";

Vue.use(Vuex);

export default new Vuex.Store({
  state: { // 对外只读，更改store中state状态的唯一方法就是提交mutation
    userInfo: {username: '', telephone: ''}, // 保存用户信息--用户名
    allWorkItem: [], // 保存从后台获取的所有工作项数据
    displayWorkItem: [], // 保存查询关键字匹配到的工作项数据
    displayWorkItemDetail: [],
    allWorkDaily: [], // 保存从后台获取的所有工作日报数据
    displayWorkDaily: [], // 保存查询匹配到的工作日报数据
    displayWorkDailyDetail: [], // 保存工作日报的详细数据
    judgeAdd: true, // 如果为true则为新增
    tabBarShow: false // 是否显示tabbar
  },
  mutations: {
    changeUserInfo(state, data) {
      state.userInfo.username = data;
    },
    storeAllWorkItem(state, data){
      state.allWorkItem = data;
    },
    storeDisplayWorkItem(state, data) {
      state.displayWorkItem = data;
    },
    storeDisplayWorkItemDetail(state, data) {
      state.displayWorkItemDetail = data;
    },
    resetWorkDaily(state){
      state.displayWorkDaily = [];
      state.allWorkDaily = [];
    },
    storeAllWorkDaily(state, data) {
      state.allWorkDaily.push(data);
    },
    storeDisplayWorkDaily(state, data) {
      state.displayWorkDaily.push(data);
    },
    storeDisplayWorkDailyDetail(state, data) {
      state.displayWorkDailyDetail = data;
    },
    changeJudgeAdd(state, data){
      state.judgeAdd = data;
    },
    showTab(state, data) {
      state.tabBarShow = data;
    }
  },
  getters: {
    // 请求工作简介查询接口
    doneDisplayWorkItem: () => (key) => {
      var data = [];
      var parmas = {query: key};
      api.get('/iims/intros/query?', parmas).then(res => {
        if (res.status === 1) {
          res.data.some((item) => {
            data.push(item);
          });
        }
      })
        .catch(err => {
          console.log(err);
        });
      return data;
    },
    // 请求工作日报查询接口
    doneDisplayWorkDaily: () => (key) => {
      var data = [];
      var parmas = {query: key};
      api.get('/iims/dailies/query?', parmas).then(res => {
        if (res.status === 1) {
          res.data.some((item) => {
            data.push(item);
          });
        }
      })
        .catch(err => {
          console.log(err);
        });
      return data;
    }
  },

  actions: {
    // 从Local Storage获取用户名并保存
    saveUserInfo(context) {
      var userData = JSON.parse(localStorage.getItem('userInfo')); // 获取登录用户信息
      var username = userData.data[0].username;
      context.commit('changeUserInfo', username);
    }
  }
});
