<template>
  <div v-show="visible" :class="myClass">
    <div class="ivu-message-notice" style="height: 50px;">
      <div class="ivu-message-notice-content">
        <div class="ivu-message-notice-content-text">
          <div :class="classes" class="ivu-message-custom-content ivu-message-success">
            <i :class="typeTips" class="fa " aria-hidden="true"></i>
            <span style="color: rgb(56, 56, 56);margin-left: 10px">{{message}}</span>
            <br/>
            <span style="color: rgb(56, 56, 56)">{{enMessage}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      visible: false,
      message: "",
      enMessage: "",
      type: "",
      myClass: "ivu-message",
      flag: 0
    };
  },
  computed: {
    classes: function () {
      var _this = this;
      if (_this.type === 'success') {
        return 'success';
      } else if (_this.type === "error") {
        return 'error';
      } else if (_this.type === 'warning') {
        return 'warning';
      }
    },
    typeTips: function () {
      var _this = this;
      if (_this.type === 'success') {
        return 'fa-check-circle';
      } else if (_this.type === 'error') {
        return 'fa-times-circle';
      } else if (_this.type === 'warning') {
        return 'fa-exclamation-circle';
      }
    }
  }
};
</script>
<style lang="css" scoped>
  /*提示*/

  .ivu-message {
    top: 0px;
    animation: mymove 0.2s linear 1;
    -webkit-animation: mymove 0.2s linear 1; /*Safari and Chrome*/
  }

  .hideClass {
    top: 0.4rem;
    animation: hide 0.2s linear 1;
    -webkit-animation: hide 0.2s linear 1; /*Safari and Chrome*/
  }

  @keyframes hide {
    from {
      top: 0.4rem;
      opacity: 1
    }
    to {
      top: 0px;
      opacity: 0
    }

  }

  @-webkit-keyframes hide /*Safari and Chrome*/
  {
    from {
      top: 0.4rem;
      opacity: 1
    }

    to {
      top: 0px;
      opacity: 0
    }
  }

  @keyframes mymove {
    from {
      top: 0px;
      opacity: 0
    }
    to {
      top: 0.4rem;
      opacity: 1
    }
  }

  @-webkit-keyframes mymove /*Safari and Chrome*/
  {
    from {
      top: 0px;
      opacity: 0
    }
    to {
      top: 0.4rem;
      opacity: 1
    }
  }

  .success {
    color: #19be6b;
  }

  .error {
    color: #ed3f14;
  }

  .warning {
    color: #ff9900;
  }

  .ivu-message, .hideClass {
    font-size: 0.6rem;
    position: fixed;
    z-index: 1010;
    width: 100%;
    top: 0.4rem;
    left: 0;
    pointer-events: none;
  }

  .ivu-message-notice:first-child {
  }

  .ivu-message-notice {
    padding: 8px;
    text-align: center;
    -webkit-transition: height 0.3s ease-in-out, padding 0.3s ease-in-out;
    transition: height 0.3s ease-in-out, padding 0.3s ease-in-out;
  }

  .ivu-message-notice-content {
    display: inline-block;
    pointer-events: all;
    padding: 0.3rem 0.8rem;
    border-radius: 4px;
    -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
    background: #fff;
    position: relative;
  }

  .ivu-message-notice-content-text {
    display: inline-block;
  }
</style>

