import Vue from 'vue';
import App from './App.vue';
import { router } from './router';
import store from './store';
import axios from 'axios';
import VueAxios from 'vue-axios';
import './assets/js/global/adjust';
import './assets/css/style.less';
// 引入接口封装api文件
import api from './api/index.js';
// 引入自定义的toast组件
import myToast from './components/common/mytoast';
import 'font-awesome/css/font-awesome.min.css';
// 引入cookie
import Cookies from 'js-cookie';
import md5 from 'js-md5';
import Calendar from 'vue-mobile-calendar'; // 引入日期选择器

// vant组件按需引用
import { loading, Popup, Image, DatetimePicker, Picker,
    Button, Field, Checkbox, Search, Icon,
    Cell, CellGroup, Toast, RadioGroup, Radio,
    NavBar, Tabbar, TabbarItem, Row,
    Col, List, Loading} from 'vant';
Vue.use(myToast);
Vue.use(Cookies);
Vue.use(Calendar);
Vue.use(Button).use(Popup)
    .use(loading)
    .use(Icon)
    .use(Field)
    .use(Checkbox)
    .use(Cell)
    .use(CellGroup)
    .use(Toast)
    .use(RadioGroup)
    .use(Radio)
    .use(NavBar)
    .use(Tabbar)
    .use(TabbarItem)
    .use(Row)
    .use(Col)
    .use(Search)
    .use(DatetimePicker)
    .use(Image)
    .use(Picker)
    .use(List)
    .use(Loading);


Vue.use(VueAxios, axios);
Vue.prototype.$axios = axios;
Vue.prototype.$api = api;
Vue.prototype.$md5 = md5;
Vue.config.productionTip = false;

let vue = new Vue({
    router,
    store,
    render: h => h(App),
    data() {
        return {
        };
    }
}).$mount('#app');


router.beforeEach((to, from, next) => {
    const title = to.meta.title;
    if (title) {
        document.title = title;
    }
    if (to.query.isFail) {
        vue.$myToast('请重新登录', 'error', '1500');
    }
    if (to.meta.isLogin) {
        if (api.getCookie('token')) {
            next();
        } else {
            next('/');
            vue.$myToast('请重新登录', 'error', '1500');
        }
    } else {
        next();
    }
});

