import { router } from '../router';
import { Toast } from 'vant';
import myToast from '../components/common/mytoast';
// 配置API接口地址
var root = 'http://47.106.83.135'
// var root = 'http://localhost:53000'
// 引用axios
var axios = require('axios');

// 设置cookie
function setCookie(key, value) {
  var exdate = new Date();
  exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * 7); //保存的天数
  document.cookie = key + "=" + value + ";path=/;expires=" + exdate.toLocaleString();
}

// 获取cookie
function getCookie(key) {
  var arr, reg = new RegExp("(^| )" + key + "=([^;]*)(;|$)");
  return (arr = document.cookie.match(reg)) ? unescape(arr[2]) : null;
}

// 清除cookie
function clearCookie(key) {
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval = getCookie(key);
  if (cval != null)
    document.cookie = key + "=" + cval + ";expires=" + exp.toGMTString();
}

// 添加请求拦截器
axios.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  var token = getCookie("token");
  if (token) {
    config.headers['Authorization'] = 'Basic ' + token;
  }
  return config;
}, function (error) {
  // 对请求错误做些什么
  console.log("124553", error.response.status);
  return Promise.reject(error);
});

// 添加响应拦截器
axios.interceptors.response.use( response => {
  // 响应处理
  return response;
}, error => {
  // 响应错误处理
  var status = error.response.status;
  if (status === 400 || status === 401) {
    router.push({
      path: '/',
      query: {
        isFail: true
      }
    });
  }
  return Promise.reject(error);
});

// axios封装
function apiAxios(method, url, params) {
  return axios({ // 注意要加return
    method: method,
    url: url,
    cache: false,
    data: method === 'POST' || method === 'PUT' ? params : null,
    params: method === 'GET' || method === 'DELETE' ? params : null,
    withCredentials: true
  })
    .then(function (res) {
      return res.data;
    })
    .catch(function (err) {
      let res = err.response;
      if (err) {
        console.log(err);
      }
      return res;
    });
}

// 返回在vue模板中的调用接口
export default {
  get: function (url, params) {
    return apiAxios('GET', url, params);
  },
  post: function (url, params) {
    return apiAxios('POST', url, params);
  },
  put: function (url, params) {
    return apiAxios('PUT', url, params);
  },
  delete: function (url, params) {
    return apiAxios('DELETE', url, params);
  },
  setCookie,
  getCookie,
  clearCookie
};
