<template>
  <div id="app">
    <!--<div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>-->
    <router-view v-if="isAlive"></router-view>
    <tabbar :active="tabIndex" v-if="tabBarShow"></tabbar>
  </div>
</template>
<script>
import tabbar from './components/tabbar';
export default {
    provide() {
        return {
            reload: this.reload
        };
    },
    components: {
        tabbar: tabbar
    },
    data() {
        return {
            isAlive: true,
            tabIndex: 1
        };
    },
    computed: {
        tabBarShow() {
            return this.$store.state.tabBarShow;
        }
    },
    methods: {
        reload() { // 刷新数据
            this.isAlive = false;
            this.$nextTick(function () {
                this.isAlive = true;
            });
        }
    },
    watch: {
        $route(to){
            if (to.meta.tabBar) {
                this.tabIndex = to.meta.tabIndex;
                this.$store.commit('showTab', true);
            } else {
                this.$store.commit('showTab', false);
            }
        }
    }
};
</script>

