/*
* author:毛振
* function:全局提示框
* params:msg提示信息，type提示类型（success，error，warning），duration显示事件（毫秒）
* */
import ToastComponent from './mytoast.vue';

const myToast = {};

// 注册Toast
myToast.install = function (Vue) {
  // 生成一个Vue的子类
  // 同时这个子类也就是组件
  const ToastConstructor = Vue.extend(ToastComponent)
  // 生成一个该子类的实例
  const instance = new ToastConstructor();

  // 将这个实例挂载在我创建的div上
  // 并将此div加入全局挂载点内部
  instance.$mount(document.createElement('div'))
  document.body.appendChild(instance.$el)

  // 通过Vue的原型注册一个方法
  // 让所有实例共享这个方法
  Vue.prototype.$myToast = (msg, type, duration) => {
    if (instance.flag === 1) {

    } else {
      instance.flag = 1;
      instance.myClass = 'ivu-message';
      instance.message = msg;
      instance.type = type;
      instance.visible = true;

      setTimeout(() => {
        instance.myClass = 'hideClass';
      }, duration - 200);
      setTimeout(() => {
        instance.visible = false;
        instance.flag = 0;
      }, duration);
    }
  };
}
export default myToast;
