<template>
  <div>
    <van-tabbar v-model="activeIndex">
      <van-tabbar-item>
        <div class="active-bg" @click.stop="go(0)"></div>
        <img slot="icon"
             slot-scope="props"
             :src="activeIndex === 0 ? icon.active0 : icon.normal0">
        <span :class="[activeIndex == 0?'active-span':'default-span']">系统维护</span>

      </van-tabbar-item>
      <van-tabbar-item>
        <div class="active-bg" @click.stop="go(1)"></div>
        <img slot="icon"
             slot-scope="props"
             :src="activeIndex === 1 ? icon.active1 : icon.normal1">
        <span :class="[activeIndex == 1?'active-span':'default-span']">数据业务</span>
      </van-tabbar-item>
      <van-tabbar-item>
        <div class="active-bg" @click.stop="go(2)"></div>
        <img slot="icon"
             slot-scope="props"
             :src="activeIndex === 2? icon.active2 : icon.normal2">
        <span :class="[activeIndex == 2?'active-span':'default-span']">个人中心</span>
      </van-tabbar-item>
    </van-tabbar>
    <van-popup class="my-popup" v-model="show">
      <div class="popup-text" @click="go(3)" :class="[activeIndex == 1?'active-span':'default-span']">我的日报</div>
      <div class="" @click="go(4)" :class="[activeIndex == 4?'active-span':'default-span']">他人日报</div>
    </van-popup>
  </div>
</template>

<script>
export default {
    name: 'work_daily',
    props: ['active'],
    data() {
        return {
            icon: {
                active0: require('../assets/images/index/orange/xitong.png'),
                active1: require('../assets/images/index/orange/yewu.png'),
                active2: require('../assets/images/index/orange/yonghu.png'),
                normal0: require('../assets/images/index/gray/xitong.png'),
                normal1: require('../assets/images/index/gray/yewu.png'),
                normal2: require('../assets/images/index/gray/yonghu.png')
            },
            show: false,
            activeIndex: this.active // 避免改变父组件的值
        };
    },
    computed: {},
    mounted() {
    },
    methods: {
        go(index) {
            switch (index) {
            case 0:
                this.activeIndex = 0;
                this.$router.push({
                    path: '/system_manage'
                });
                break;
            case 1:
                this.show = true;
                // this.$router.push({
                //     path: '/work_daily'
                // });
                break;
            case 2:
                this.activeIndex = 2;
                this.$router.push({
                    path: '/MyInfo'
                });
                break;
            case 3: // 我的日报
                this.$router.push({
                    path: '/work_daily'
                });
                this.activeIndex = 1;
                this.show = false;
                break;
            case 4:
                this.$router.push({
                    path: '/other_daily'
                });
                break;
            default:
                break;
            }
        }
    },
};
</script>

<style lang="less" scoped>
  .van-tabbar {
    height: 50px;
    box-shadow: 0px -3px 11px 1px rgba(163, 163, 163, 0.49);
    .van-tabbar-item--active {
      color: #fe8f01;
    }
    .van-tabbar-item{
      font-size: 0.55rem;
      position: relative;
    }
    .active-bg{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
      z-index: 99999;
    }
    .active-span{
      color: #fe8f01;
    }
    .default-span{
      color: #7d7e80;
    }
  }
  /deep/ .my-popup.van-popup--center {
    z-index: 2012;
    left: 33vw;
    width: 33vw;
    bottom: 50px;
    top: auto;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 3px 1px;
    box-sizing: border-box;
    transform: unset;
  }
  /deep/ .van-overlay{
    background-color: rgba(0, 0, 0, 0);
  }
  .van-tabbar-item__icon img {
    display: block;
    width: 0.8rem;
    height: auto;
  }
  .my-popup{
    font-size: 0.55rem;
    div{
      height: 35px;
      line-height: 35px;
    }
    .popup-text{
      border-bottom: 1px solid #fe8f01;
    }
  }
  .active-span{
    color: #fe8f01;
  }
  .default-span{
    color: #7d7e80;
  }
</style>
